import React from 'react';
class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            CT: 25,
            HT: 80,
            T: 150,
            TT: 70,
            error: true,
            error_msg: "",
        };
    }

    handleClick = () => {
        if (this.state.TT > this.state.HT) {
            this.setState({ error_msg: "The hot water temperature must be greater than the target temperature" })
        }
        if (this.state.TT < this.state.CT) {
            this.setState({ error_msg: "The target temperature must be greater than the cold water temperature" })
        }
        var c = this.state.CT;
        var h = this.state.HT;
        var t = this.state.T;
        var tt = this.state.TT;

        this.setState({
            error: false,
            error_msg: "",
            CTML: Round(t * (tt - h) / (c - h), 2),
            HTML: Round(t * (tt - c) / (h - c), 2),
        })
    }

    handleChange = (name, value) => {
        console.log(name);
        console.log(value);
        this.setState({ [name]: value })
    }

    render() {
        return (
            <div className="col-lg-12">
                <h2>Milk Calculator</h2>
                <CT name="CT" defaultV={this.state.CT} changHandler={this.handleChange} />
                <HT name="HT" defaultV={this.state.HT} changHandler={this.handleChange} />
                <T name="T" defaultV={this.state.T} changHandler={this.handleChange} />
                <TT name="TT" defaultV={this.state.TT} changHandler={this.handleChange} />
                <button className="btn btn-sm btn-success cus" onClick={this.handleClick}>Calculate</button >
                {
                    this.state.error ? <Error text={this.state.error_msg} /> : <Display CTML={this.state.CTML} HTML={this.state.HTML}/>
                }
            </div>
        )
    }
}

class CT extends React.Component {
    handleChange = (evt) => {
        this.props.changHandler(this.props.name, evt.target.value);
    }
    render() {
        return (
            <>
                Cold Water Temperature(°C)
                <select className="form-control" type="number" value={this.props.defaultV} onChange={this.handleChange}>
                    {[...new Array(101)].map((e, i) => <option key={`ct${i}`}>{i}</option>)}
                </select>
            </>
        )
    }
}
class HT extends React.Component {
    handleChange = (evt) => {
        this.props.changHandler(this.props.name, evt.target.value);
    }
    render() {
        return (
            <>
                Hot Water Temperature(°C)
                <select className="form-control" type="number" value={this.props.defaultV} onChange={this.handleChange}>
                    {[...new Array(101)].map((e, i) => <option key={`ht${i}`}>{i}</option>)}
                </select>
            </>
        )
    }
}
class T extends React.Component {
    handleChange = (evt) => {
        this.props.changHandler(this.props.name, evt.target.value);
    }
    render() {
        return (
            <>
                How much water do you need?(ml)
                <select className="form-control" type="number" value={this.props.defaultV} onChange={this.handleChange}>
                    {[...new Array(40)].map((e, i) => <option key={`t${i}`}>{(i + 1) * 10}</option>)}
                </select>
            </>
        )
    }
}
class TT extends React.Component {
    handleChange = (evt) => {
        this.props.changHandler(this.props.name, evt.target.value);
    }
    render() {
        return (
            <>
                Target Temperature(°C)
                <select className="form-control" type="number" value={this.props.defaultV} onChange={this.handleChange}>
                    {[...new Array(101)].map((e, i) => <option key={`tt${i}`}>{i}</option>)}
                </select>
            </>
        )
    }
}
class Display extends React.Component {
    render() {
        return (
            <>
                <div className="text-primary font-weight-bold h2">
                    Cold Water: {this.props.CTML} ml
                </div>
                <div className="text-danger font-weight-bold h2">
                    Hot Water: {this.props.HTML} ml
                </div>
            </>
        )
    }
}

class Error extends React.Component {
    render() {
        return (
            <div>{this.props.text}</div>
        )
    }
}

function Round(number, decimalpoint) {
    if (!number) return number;
    var dp = Math.pow(10, decimalpoint);
    return Math.round(number * dp) / dp;
}
export default App;
